  position: relative;
  height: 100%;
  overflow-y: scroll;
  flex-shrink: 0;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  flex-direction: column;
  flex-grow: 1;
  background-color: #f4f5f5;
}
.pdf-page {
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
  margin: 40px 0;
  position: relative;
}
.highlight-block {
  font-size: 10px;
  color: rgba(0, 0, 0, 0.2)
}
.flex {
  display: flex;
  align-items: start;
  justify-content: start;
  box-sizing: border-box;
  height: 100%;
}
.res-list {
  width: 30%;
  height: 100%;
  flex-shrink: 0;
  padding: 20px;
  overflow-y: scroll;
  box-sizing: border-box;
}
.res-page {
  padding: 20px;
}
.page-num {
  margin-bottom: 20px;
  text-align: center;
  font-weight: bold;
}
.res-item {
  margin-bottom: 20px;
  border-radius: 4px;
  word-break: break-all;
  padding: 10px;
  box-shadow: 0 0 3px rgba(0, 0, 0, 0.2);
}
.res-item:hover {
  background-color: #f4f5f5;
  cursor: pointer;
}
.res-item.active {
  background-color: #e6f7ff;
}
.content {
  word-break: break-all;
